import App from "./Altida.svelte";
const initConfig = {
    mainTitle: "Customize your Accora Altida",
    bookADemoHref: "https://us.accora.care/book-a-demo",
};
export const AltidaConfigurator = (targetId, config) => {
    const app = new App({
        target: document.getElementById(targetId),
        props: {
            config: Object.assign(Object.assign({}, initConfig), config),
        },
    });
    return app;
};
window.AltidaConfigurator = AltidaConfigurator;
export default AltidaConfigurator;
